// width of site
$site-width: 82em;
// breakpoints used by media query mixins
// NOTE: If these values are changed, the comments in _grid.scss must also be changed
$xs: 20em;
$s: 30em;
$m: 52.5em;
$l: 77.5em;
$xl: 112.5em;
$xxl: 120em;

$mob-bp: $l;

$mob-head-height: 5em;

@mixin body-padding() {
	padding-left: 1em;
	padding-right: 1em;
	@include mq($m) {
		padding-left: 2em;
		padding-right: 2em;
	}
}

body {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	min-height: 100vh;
	max-width: $site-width;
	margin: 0 auto;
	@include body-padding();
	padding-top: $mob-head-height;

	@include mq($mob-bp) {
		padding-top: 0;
	}
}

main {
	flex: 1 0 auto;
	// padding: 1.5em 0;

	@include mq($l) {
		// padding: 3em 0;
	}
}

.wrapper {
	width: 100%;
	max-width: $site-width;
	margin: 0 auto;
	@include body-padding();
}

// https://codepen.io/matthewbeta/pen/ZEbmJjw
$flex-grid-modifier: calc(#{$m} - 100%);
$margin: 1rem;

@supports ( (--a: 0)) {
	:root {
		--modifier: calc(#{$m} - 100%);
		--margin: 1rem;
	}
}

.flex-grid {
	display: flex;
	margin-top: -$margin;
	margin-left: -$margin;

	@supports ( (--a: 0)) {
		margin-top: calc(var(--margin) * -1);
		margin-left: calc(var(--margin) * -1);
	}
	
	&--v-center {
		align-items: center;
	}

	&--baseline {
		align-items: baseline;
	}

	&--split {
		justify-content: space-between;
	}

	&--reverse {
		flex-direction: row-reverse;
	}

	&--even {
		> .flex-grid__item {
			flex-basis: 100%;
		}
	}
	
	&--fill {
		> .flex-grid__item {
			flex-grow: 1;
		}
	}
	
	&--responsive {
		flex-wrap: wrap;
		
		> .flex-grid__item {
			flex-grow: 1;
			flex-basis: calc(#{$flex-grid-modifier} * 999);
			@supports ( (--a: 0)) {
					flex-basis: calc(var(--modifier) * 999);
			}
		}
	}
	
	&--2-col-wrap {
		> .flex-grid__item {
			min-width: calc(50% - #{$margin});
			flex-basis: calc(#{$flex-grid-modifier} * 999);
			@supports ( (--a: 0)) {
				min-width: calc(50% - var(--margin));
				flex-basis: calc(var(--modifier) * 999);
			}
			flex-grow: initial;
		}
	}

	&--3-up {
		flex-wrap: wrap;
		> .flex-grid__item {
			max-width: calc(33.33% - #{$margin});
			@supports ( (--a: 0)) {
				max-width: calc(33.33% - var(--margin));
			}
			flex-grow: initial;
		}
	}
	
	&--products {
		@include mq($mob-bp) {
			--margin: 2em;
		}
		> .flex-grid__item {
			flex-basis: calc((#{$flex-grid-modifier} * 2) * 999);
			min-width: calc(33.33% - #{$margin});
			max-width: calc(50% - #{$margin});
			flex-grow: initial;
			@supports ( (--a: 0)) {
					flex-basis: calc((var(--modifier) * 2) * 999);
					min-width: calc(33.33% - var(--margin));
					max-width: calc(50% - var(--margin));
			}
		}
	}
	
	&--ctas {
		> .flex-grid__item {
			flex-basis: calc(#{$flex-grid-modifier} * 999);
			min-width: calc(33.33% - #{$margin});
			flex-grow: initial;
			@supports ( (--a: 0)) {
					flex-basis: calc((var(--modifier) * 2) * 999);
					min-width: calc(33.33% - var(--margin));
			}
		}
	}

	> * {
		margin-top: $margin;
		margin-left: $margin;

		@supports ( (--a: 0)) {
			margin-top: var(--margin);
			margin-left: var(--margin);
		}
	}
	
	&__item {
		//  demo style
		// padding: 1em;
		// min-width: 100px;
		// min-height: 4em;
		// text-align: center;
		// background: pink;
		// border: 4px solid;
		
		// real styles 		
		// margin-top: $margin;
		// margin-left: $margin;

		&--v-center {
			display: flex;
			align-items: center;
		}
		
		.flex-grid--responsive > &,
		.flex-grid--fill > & {
			// Should add up to 10 		
			@for $i from 1 through 10 {
				&--cols-#{$i} {
					flex-grow: $i;
				}
			}

		}
	}
}


.grid-crop {
	@include mq($m) {
		padding-right: 10%;
		&--80 {
			padding-right: 20%;
		}
		&--reverse {
			padding-right: 0;
			padding-left: 10%;
			&.grid-crop--80 {
				padding-left: 20%;
			}
		}
	}
}