.text-hero {
	position: relative;

	&__title,
	&__content {
		@include mq($m) {
			padding-top: 4em;
			padding-bottom: 4em;
		}
	}
	&__content {
		position: relative;
		padding-right: 4em;
	}


	&__content:after,
	&:after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		background-color: $color-cream;
	}

	&__content:after {
		height: calc(100% + 6em);
		transform: translate(-1em, -4em);
		@include mq($m) {
			display: none;
		}
	}

	&:after {
		width: 70%;
		left: auto;
		right: 0;
		display: none;
		@include mq($m) {
			display: block;
		}
	}
}