.tab-block {
	color: $color-green;
	margin-bottom: -6em;

	&__heading {
		margin-top: 1em;
	}

	&__img {
		position: relative;
		z-index: 0;
		transform: translateY(-2em) translateX(-1em);
		margin-bottom: -2em;

		@include mq($m) {
			max-width: 50%;
			transform: translateY(-10em) translateX(4em);
			margin-bottom: -10em;
		}
	}

	.tabs {
		position: relative;
		z-index: 1;
	}
}