.campaign-strip {
	margin: 4em 0;
	color: $color-pink;
	background-color: $color-maroon;

	@include mq($m) {
		background: linear-gradient(to right,$color-white 40%,$color-white 40%, $color-maroon 40%)
	}

	&__content {
		padding: 0 1em 4em 1em;

		@include mq($m) {
			padding: 4em 2em;
		}
	}

	&__img-wrap {
		width: calc(100% + 2em);
		height: calc(100% + 1.25em);
		transform: translate(-1em, -1.25em);

		@include mq($m) {

			position: relative;
			height: calc(100% + 1rem);
			width: 120%;
			transform: translateX(-20%);
			
			> * {
				position: absolute;
				top: -1rem;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}

	}

	&__img {
		object-fit: cover;
		min-height: 100%;
	}

	&__icon {
		position: relative;
		top: -6em;
		left: calc(100% - 6em);
		max-width: 6em;

		@include mq($m) {
			left: auto;
			top: -2em;
		}
	}
}