.site-header {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1;
	width: 100%;
	height: $mob-head-height;
	@include body-padding;
	padding-top: 1em;
	padding-bottom: 1em;
	box-shadow: 0 1px 3px transparentize($color-black, .85);

	
	&:after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		background-color: $color-white;
	}

	@include mq($mob-bp) {
		position: relative;
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		height: auto;
		box-shadow: initial;
		padding-left: 0;
		padding-right: 0;
	}

	&__logo {
		display: block;
		margin: .5em auto 0;
		width: 50%;
		max-width: 160px;


		@include mq($mob-bp) {
			margin: 0;
			max-width: 200px;
		}
	}
}