/* @import must be at top of file, otherwise CSS will not work */
@import url("https://hello.myfonts.net/count/3b9d8a");

@font-face {
	font-family: 'bartender';
	src: url('bartender.woff2') format('woff2'),
	url('bartender.woff') format('woff');
	font-weight: normal;
	font-style: normal;
	color: $color-text;
}

$font-body:  input-serif, courier-new, serif;
$font-heading: 'bartender', serif;

$weight-bold: 700;
$weight-regular: 400;


body {
	text-rendering: optimizeSpeed;
	text-rendering: geometricPrecision;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	font-family: $font-body;
	font-weight: $weight-regular;
	color: $color-black;
	font-size: .875em;

	@include mq($l) {
		font-size: 1em;
	}
}