.site-nav {
	visibility: hidden;
	position: fixed;
	top: $mob-head-height;
	left: 0;
	z-index: -2;
	width: 100%;
	height: calc(100% - #{$mob-head-height});
	display: flex;
	flex-direction: column;
	line-height: 1;
	color: $color-yellow;
	background: $color-black;
	@include body-padding;
	padding-top: $mob-head-height;
	padding-bottom: 1.5em;
	overflow: auto;
	transform: translateY(-100%);
	transition: transform .2s ease-in-out, visibility 0s linear .2s;
	
	&.is-open {
		visibility: visible;
		transform: translateY(0);
		transition: visibility 0s linear, transform .2s ease-in-out;
	}

	> * {
		flex: 1 0 auto;
	}

	@include mq($mob-bp) {
		position: static;
		visibility: visible;
		flex-direction: column-reverse;
		align-items: flex-end;
		width: auto;
		height: auto;
		z-index: initial;
		transform: initial;
		padding: 0;
		background-color: transparent;
		color: inherit;
		overflow: visible;
	}

	&__links {
		font-size: 2em;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		
		@include mq($mob-bp) {
			flex-direction: row;
			font-size: 1em;
		}
	}
	
	&__link {
		font-family: $font-heading;
		text-transform: uppercase;
		color: inherit;
		font-size: 1.875em;
		line-height: .8;
		border-bottom: 7px solid transparent;
		padding-bottom: .25em;
		
		& + & {
			margin-top: 1rem;
		}
		
		&:hover, 
		&.is-active {
			border-color: $color-white;
			color: $color-white;
			
			@include mq($mob-bp) {
				color: inherit;
				border-color: inherit;
			}
		}
		
		@include mq($mob-bp) {
			margin-top: .1em;
			padding-bottom: .5em;
			& + & {
				margin-top: .1em;
				margin-left: 1.5em;
			}
		}
	}

	&__toggle {
		-webkit-appearance: none;
		position: absolute;
		top : 1.5em;
		left: 1em;
		width: 2em;
		width: 2em;
		padding: 0;
		margin: 0;
		border: 0;
		background: transparent;
		color: $color-text;

		&:hover {
			color: $color-green;
		}

		> svg {
			display: block;
			width: 100%;
			g {
				fill: currentColor;
			}
		}
	}

}


.js-mobile-nav-open, 
.js-mobile-nav-open body {
	overflow: hidden;
	max-height: 100%;

	main,
	footer {
		pointer-events: none;
		> * {
			opacity: 0;
		}
	}
}