.poster {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	transform: translateX(-1em);
	width: calc(100% + 2em);
	padding: 8em 1em 2em;
	color: $color-yellow;

	@include mq($m) {
		transform: initial;
		width: 100%;
		padding: 4em 10%;

		margin-top: 4em;
		margin-bottom: 4em;
	}

	&__caption {
		margin-top: 0;
		margin-left: 0;
		margin-right: 0;
		word-spacing: 40em; 
	}

	&__img {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}