.product-cta {
	display: flex;
	flex-direction: column;
	justify-content: space-between;	
	max-width: 40em;
	height: 100%;
	margin: 0 auto;
	text-align: center;

	&__img {
		width: 100%;
		margin: 0 auto;
	}

	&--divider {
		&:after {
			content: "";
			display: table;
			width: 2.875em;
			height: 7px;
			background-color: $color-text;
			margin: 5em auto 3em;
		}
	}

}