.hidden {

	display: none !important;
	visibility: hidden;
  &--xs {
    @include mq(0, $s) {
      display: none !important;
      visibility: hidden;
    }
  }
  &--s {
    @include mq($s, $m) {
      display: none !important;
      visibility: hidden;
    }
  }
  &--m {
    @include mq($m, $l) {
      display: none !important;
      visibility: hidden;
    }
  }
  &--l {
    @include mq($l, $xl) {
      display: none !important;
      visibility: hidden;
    }
  }
  &--xl {
    @include mq($xl, $xxl) {
      display: none !important;
      visibility: hidden;
    }
  }
  &--xxl {
    @include mq($xxl) {
      display: none !important;
      visibility: hidden;
    }
	}
	&--js {
		.js & {
			display: none !important;
			visibility: hidden;
		}
	}
	&--no-js {
		.no-js & {
			display: none !important;
			visibility: hidden;
		}
	}

	&--mob {
		@include mq(0, $mob-bp) {
			display: none;
			visibility: hidden;
		}
	}

	&--desk {
		@include mq($mob-bp) {
			display: none;
			visibility: hidden;
		}
	}
}

.visuallyhidden {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
	clip: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	position: static;
	width: auto;
}

.text-caps {
	text-transform: uppercase;
}
.text-center {
	text-align: center;
}
.text-small {
	font-size: .8571428571em;
}
.text-bold {
	font-weight: $weight-bold;
}


.full-bleed {
  width: 100vw;
	margin-left: calc(50% - 50vw);
}

