::-webkit-input-placeholder {
	/* WebKit browsers */
	color: $color-placeholder;
}
:-moz-placeholder {
	/* Mozilla Firefox 4 to 18 */
	color: $color-placeholder;
}
::-moz-placeholder {
	/* Mozilla Firefox 19+ */
	color: $color-placeholder;
}
:-ms-input-placeholder {
	/* Internet Explorer 10+ */
	color: $color-placeholder;
}

.form {
	&__row {
		overflow: visible;

		& ~ & {
			margin-top: 1.5em;
		}
	}
	&__label {
		display: block;
		margin-bottom: 0.5em;
		color: $color-green;

		.form__row--error & {
			color: $color-error;
		}
	}

	&__field {
		box-shadow: none;
		border: 1px solid $color-green;
		border-radius: 4px;
		padding: 0.8em 1em;
		font-size: 16px;
		font-family: inherit;
		width: 100%;
		transition: border-color 300ms ease-in-out, color 300ms ease-in-out, background-color 300ms ease-in-out;
		-webkit-appearance: none;
		border-radius: 4px;

		&--small {
			width: 6em;
		}

		&--textarea {
			resize: vertical;
		}

		&--file {
			color: $color-text;
		}

		.form__row--error & {
			border-color: $color-error;
			background: lighten($color-error, 50)
		}
	}

	&__select {
		display: block;
		font-size: 16px;
		font-weight: 300;
		width: 100%;
		max-width: 100%; 
		padding-right: 3em;
		-moz-appearance: none;
		-webkit-appearance: none;
		appearance: none;

		
		background-position: calc(100% - 1em) calc(50% - 1em);

		&::-ms-expand {
			display: none;
		}
		
		& > option {
			font-weight:normal;
		}
			
	}

	&__error-msg {
		color: $color-error;
		margin-top: 0.5em;
		display: none;

		.form__row--error & {
			display: block;
		}
	}
	&__msg {
		display: block;
		margin-top: 0.5em;
	}

	&__hp {
		display: none; // HONEYPOTS
	}
}

fieldset {
	padding: 0;
	margin: 0;
	border: 0 none;
}

// For shopify theme-address JS
[data-address='root'] [data-aria-hidden='true'] {
  display: none;
}