.home-hero {

		padding: 2em 0;
		@include mq($m) {
			padding: 4em 0;
		}

	&__img {
		width: calc(100% + 1em);
		transform: translateX(-1em);
		margin-bottom: -6em;
		z-index: -1;
		@include mq($m) {
			margin-bottom: initial;
			width: 125%;
			transform: translateY(-2em);
		}
	}

	&__cta {
		position: relative;
		z-index: 0;
		margin: 0 auto;
		@include mq($m) {
			margin: 0;
		}
	}

	&__img-2 {
		width: calc(100% + 1em);
		transform: translateY(1em);
		@include mq($m) {
			width: 100%;
			transform: intital;
		}
	}

	&__copy {
		padding-right: 4em;
	}

	&__offset-bg {
		position: relative;
		&:after {
			content: "";
			background-color: $color-cream;
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;
			transform: translateY(-14em);
		}
	}
}