.cart-link {
	position: absolute;
	top: 1.5em;
	right: 1em;
	display: block;
	width: 2em;
	color: $color-text;

	@include mq($mob-bp) {
		position: relative;
		top: .125em;
		right: auto;
	}

	&:hover {
		color: $color-green;
	}
	
	> svg {
		display: block;
		width: 100%;
		path {
			fill: currentColor;
		}
	}

	&__count {
		position: absolute;
		top: 50%;
		right: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		background: $color-teal;
		border-radius: 100%;
		width: 1.5em;
		height: 1.5em;
		transform: translate(50%, -35%);
	}
}