.account-nav {
	display: flex;
	flex-direction: column;
	align-items: baseline;
	text-transform: uppercase;
	margin-top: 2em;

	> * + * {
		margin-top: 1em;
	}

	@include mq($mob-bp) {
		flex-direction: row;
		margin-top: 0;
		margin-bottom: 1.5em;

		> * + * {
			margin-top: 0;
			margin-left: 1.5em;
		}

	}

	> a:not([class]) {
		text-decoration: none;

		@include mq($mob-bp) {
			font-size: .75em;
		}

		&:hover {
			text-decoration: underline;
		}
	}
}