.tabs {

	&__tab-list {
		display: flex;
		flex-wrap: wrap;
		list-style: none;
		margin: 0 0 2em -1em;
		padding: 0;
		
		> * {
			margin-left: 1em;
			margin-top: 1em
		}
	}

	&__trigger {
		display: block;
		text-decoration: none;
		text-transform: uppercase;
		font-weight: $weight-bold;
		color: $color-green;
		border-bottom: 7px solid transparent;

		
		&.is-selected,
		&:hover {
			border-color: $color-teal;
		}
	}

	&__panel {

		display: none;
		padding: 4em 2em;
		width: calc(100% + 1em);
		color: $color-white;
		background-color: $color-green;

		.is-initialized & {
				display: inherit;
		}

		&.is-hidden {
				display: none;
		}
	}

	&__content {
		padding-top: 4em;
	}

}