.cart {
	&__item {
		padding: 1.5em 0;
		border-bottom: 1px solid $color-grey;
	}

	&__item-title {
		color: inherit;
		
		&:hover {
			text-decoration: underline;
		}
	}

	&__item-remove {
		display: block;
		color: inherit;

		&:hover {
			text-decoration: underline;
			color: $color-error;
		}
	}

	&__summary {
		padding-top: 1.5em;
	}
}