.breadcrumbs {
	padding-top: 1.5em;
	color: $color-green;

	&:before {
		content: '';
		display: inline-block;
		vertical-align: middle;
		position: relative;
		top: -0.125em;
		left: -0.5em;
		transform: rotate(-135deg);
		height: 0.65em;
		width: 0.65em;
		margin-left: 1em;
		border-style: solid;
		border-width: 2px 2px 0 0;
		transition: transform .2s ease
	}

	a {
		color: inherit;
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}
}