.heading {
	font-family: $font-heading;
	text-transform: uppercase;
	line-height: .6;
	margin-bottom: .4em; // Accounts for weird line height

	&--h1 {
		font-size: 6.1428571429em;
		@include mq($m) {
			font-size: 13.5em;
		}
	}
	&--h2 {
		font-size: 4.8571428571em;
		@include mq($m) {
			font-size: 6.75em;
		}
	}
	&--h3 {
		font-size: 3.5714285714em;
		@include mq($m) {
			font-size: 3.5em;
		}
	}
	&--h4 {
		font-size: 2.1428571429em;
		@include mq($m) {
			font-size: 2.75em;
		}
	}
}