.short-text {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding-right: 1em;
	height: 100%;
	
	&:after {
		content: "";
		display: inline-block;
		width: 2.875em;
		height: 7px;
		background-color: $color-text;
		margin: 1.5em 0;
	}

	&__wrap { // for when wrapped beacuse its a shopify section
		height: 100%;
	}
}