.radio {
	position: relative;

	> label {
		display: inline-block;
		display: flex;
		flex: 1 0 auto;
		height: 100%;
		justify-content: center;
		align-items: center;
		width: 100%;
		padding: 1.1em 1.5em .9em;
		line-height: 1;
		text-align: center;
		color: $color-green;
		background-color: $color-white;
		border-radius: 4px;
		border: 1px solid $color-green;

	}

	> input {
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
	}

	> input:checked + label {
		color: $color-white;
		background-color: $color-green;

	}

	> label:hover, 
	input:focus + label {
		color: $color-green;
		background: $color-teal;
	}
}